@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: white;
  overflow: hidden
}

/* Active Navigation Link */
.active {
  @apply text-white;
}

.active svg {
  @apply text-white;
}

.active .playlist {
  @apply bg-[#222222]
}

.active .artist {
  @apply bg-[#222222]
}

.bar:nth-child(2) {
  animation-delay: 0.2s;
}

.bar:nth-child(3) {
  animation-delay: 0.4s;
}

.bar:nth-child(4) {
  animation-delay: 0.6s;
}

.bar:nth-child(5) {
  animation-delay: 0.8s;
}

.bar:nth-child(6) {
  animation-delay: 1s;
}

.smooth-transition {
  transition: all 0.3s ease-in-out;
}

::-webkit-scrollbar {
  width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffffff40;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ffffff60;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hide-scrollbar {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}